<template>
  <div class="loginBox">
    <div class="title">登录</div>
    <text-input :obj="user" :attr="'sno'" :defualt="'学号'"></text-input>
    <password-input :obj="user" :attr="'pwd'" :default="'密码'" :events="pwdEvents"></password-input>
    <button @click="submit">确定</button>
    <a @click="$router.push('/login/register')">没有账号？立即注册</a>
  </div>
</template>

<script>
import PasswordInput from './PasswordInput.vue';
import TextInput from './TextInput.vue';

export default {
  name: 'LoginBox',
  data () {
    return {
      user: {
        sno: '',
        username: '',
        pwd: ''
      },
    };
  },
  methods: {
    submit () {
      let that = this;
      this.$store.dispatch('login', {
        sno: this.user.sno,
        pwd: this.user.pwd,
        callback: function (res) {
          if (res) {
            that.$router.push('/home');
          } else {
            alert('用户名或密码错误');
          }
        }
      });
    },
  },
  computed: {
    pwdEvents () {
      let that = this;
      return [{
        type: 'keydown',
        fun: function (e) {
          if (e.key == 'Enter') {
            that.submit();
          }
        },
      }]
    }
  },
  components: {
    TextInput,
    PasswordInput
  },

}
</script>

<style>
.loginBox {
  margin: 160px auto;
  width: 360px;
  text-align: center;
}
</style>


